import React from 'react';
import classes from './Dialog.module.css';
import DialogItem from './DialogItem/DialogItem';
import Message from './Message/Message';
import { Navigate } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, minLengthCreator, required } from '../../utils/validators/validators';
import { Textarea } from '../common/FormControls/FormControls';

const Dialog = (props) => {

  let dialogsElements = props.dialogPage.dialogs.map(d => (<DialogItem name={d.name} key={d.id} id={d.id} />));
  let messagesElements = props.dialogPage.messages.map(m => (<Message message={m.message} key={m.id} />));

  let addNewMessage = (values) => {
    props.sendMessage(values.newMessageBody);
  }

  if (!props.isAuth) return <Navigate to={'/login'} />;

  return <div className={classes.dialogs}>
    <div className={classes.dialogsItems}>
      {dialogsElements}
    </div>
    <div className={classes.messages}>
      <div>
        <AddMessageFormRedux onSubmit={addNewMessage} />
      </div>
      {messagesElements}
    </div>
  </div>
}

const maxLength100 = maxLengthCreator(100);
const minLength2 = minLengthCreator(2);

const AddMessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field component={Textarea} validate={[required, maxLength100, minLength2]} name="newMessageBody" placeholder="Enter your message" />
      </div>
      <div>
        <button>Send</button>
      </div>
    </form>
  );
}

const AddMessageFormRedux = reduxForm({ form: 'dialogAddMessageForm' })(AddMessageForm);

export default Dialog;